/* nav mob */
.navbar__pc{
    display: none;
}

.navbar__mobile{
    grid-column: 1/-1;
    display: grid;
    background-color: black;
    padding: 1rem;
    justify-self: center;
}

.navbar__logo{
    grid-row: 1;
    justify-self: flex-start;
    font-size: 1.5rem;
    font-weight: 500;
}
.navbar__mobile--menu{
    grid-row: 1;
    justify-self: flex-end;
    font-size: 1.5rem;
    font-weight: 500;
}


/* nav pc */
@media (min-width : 1025px){
    #navbar{
        padding: 1rem;
    }
    .navbar__pc{
        display: flex;
grid-column: 1/-1;
display: grid;
grid-template-columns: repeat(11,1fr);
padding-top: 3rem;
    }
    .navbar__logo{
        grid-column: 1/3;
        justify-self: flex-start;
        margin-left: 1.5rem;

    }
    .navbar__logo a{
        font-size: 2rem;
        font-weight: 900;
    }
    .navbar__links{
        grid-column: 4/12;
        justify-self: flex-end;
    }
    .navbar__links ul{
        display: flex;
    }
    .navbar__links li{
        margin-right: 1.5rem;
    }


    .navbar__mobile{
        display: none;
    }
}