.NavMobOpen{
    background-color: black;
    width: 100vw;
    height: 100vh;
    display: grid;
    justify-content: center;
    align-content: center;
    position: relative;
}
.navMobClose{
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2.1rem;
}
.NavMobOpen li{
    margin: 2rem 0;
    font-size: 1.5rem;
    font-weight: 500;
}
.NavMobOpen ul{
  position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%,-50%);
}
