.projects__infoAndLink {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  justify-content: center;
  align-content: center;
  transform: translate(-50%, -50%);
  display: none;
}
.projects__infoAndLink .btn{
  width: 60%;
  justify-self: center;
  margin-top: 1rem;
  font-size: 90%;
  padding: 0.5rem 0;
}
.projects__container{
    justify-self: center;
    margin-bottom: 2rem;
    position: relative;
    transition: all 1s;
}
  .projects__container img{
    width: 100%;
  }

  .projects__infoAndLink h1{
    font-size: 100%;
  }


  @media (max-width:1024px){
    #projects .row{
      grid-template-columns: repeat(3, 1fr);
    }
    #projects .projects__container{
      grid-column: 1/-1;
    }
  }

  @media (min-width:1025px){
    #projects .row{
      grid-template-columns: repeat(auto-fit,minmax(400px,1fr));
      column-gap: 2rem;
    }
  }