@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
/* var */
:root {
  --mainColor: white;
  --bgColor: #101010;
  --btnColor: #e9204f;
  --servicesConColor: #1f1f1f;
  --servicesInfoColor: #747474;
}
/* reseting code  ul , a , body */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-size: 16px;
  color: white;
  color: var(--mainColor);
  background-color: #101010;
  background-color: var(--bgColor);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: white;
  color: var(--mainColor);
  position: relative;
}

/* title , p font size */
.titleFontSize {
  font-size: 2rem;
}
.normalFontSize {
  font-size: 1rem;
  color: #747474;
  margin-top: 1rem;
  margin-bottom: 3rem;
}

/* row , title , p font size and max width settings */

@media (max-width: 700px) {
  .row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: auto;
    width: 90%;
  }
  #hero,
  .navbar__mobile,
  .navbar__pc {
    width: 100vw;
  }
}

@media (min-width: 701px) and (max-width: 1024px) {
  .row {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    width: 90%;
    margin: auto;
  }
  #hero .row,
  .navbar__mobile,
  .navbar__pc {
    width: 100vw;
  }
}

@media (min-width: 1025px) {
  body{
    overflow-x: hidden;
  }
  .row {
    display: grid;
    grid-template-columns: repeat(11, 1fr);
  }
  #root {
    width: 100vw;
  }
  .row {
    width: 90%;
    margin: auto;
  }
  .titleFontSize {
    font-size: 3.5rem;
  }
  .normalFontSize {
    font-size: 1.2rem;
  }
}

/* constant like btn , services container , midLine  , team container , sml__list , inputs and textarea */
.btn {
  background-color: #e9204f;
  background-color: var(--btnColor);
  color: white;
  color: var(--mainColor);
  border: none;
  font-weight: 600;
  font-size: 1rem;
  padding: 1rem;
  font-family: "Roboto", sans-serif;
  border-radius: 0.1rem;
}

.services__container {
  background-color: #1f1f1f;
  background-color: var(--servicesConColor);
  text-align: left;
  padding: 3rem 2rem;
}
.services__info {
  color: #747474;
  color: var(--servicesInfoColor);
}
.services__title {
  font-weight: 700;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.midLine {
  background-color: #e9204f;
  background-color: var(--btnColor);
  width: 0.15rem;
  height: 4rem;
  grid-column: 1/-1;
  justify-self: center;
  margin: 4rem;
}


.team__container img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: left;
  border-radius: 50%;
}

.sml__list {
  display: flex;
}
.sml__list li {
  margin-right: 1rem;
  font-size: 1.5rem;
}
.sml__list li a i {
  border: #e9204f solid 2px;
  padding: 0.5rem;
  border-radius: 50%;
  transition: all 0.3s;
}

input {
  border: none;
  background-color: transparent;
  border-bottom: #747474 2px solid;
  border-bottom: var(--servicesInfoColor) 2px solid;
  color: white;
  color: var(--mainColor);
  padding-bottom: 1rem;
  margin: 1rem 0;
  font-size: 0.9rem;
  outline: none;
  transition: all 0.3s;
}
::-webkit-input-placeholder {
  color: #747474;
  color: var(--servicesInfoColor);
}
:-ms-input-placeholder {
  color: #747474;
  color: var(--servicesInfoColor);
}
::placeholder {
  color: #747474;
  color: var(--servicesInfoColor);
}
textarea {
  font-family: "Roboto", sans-serif;
  font-size: 0.9rem;
  background-color: transparent;
  border: none;
  color: white;
  color: var(--mainColor);
  width: 95%;
  border-bottom: 2px solid #747474;
  border-bottom: 2px solid var(--servicesInfoColor);
  outline: none;
  transition: all 0.3s;
}

/* animations and hover.. */
.btn,
a,
.services__container {
  cursor: pointer;
  transition: all 0.3s;
}
.services__title {
  transition: all 0.3s;
}

.btn:hover {
  background-color: white;
  background-color: var(--mainColor);
  color: #e9204f;
  color: var(--btnColor);
}

.navbar__links a:hover::after {
  width: 100%;
  background-color: #e9204f;
  background-color: var(--btnColor);
}
.navbar__links a::after {
  width: 0%;
  content: "";
  background-color: white;
  background-color: var(--mainColor);
  position: absolute;
  top: 1.3rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 2px;
  transition: all 0.3s;
}

.services__container:hover {
  box-shadow: 0px 0px 10px 3px rgba(255, 255, 255, 0.1);
}
.services__container:hover .services__title {
  color: #e9204f;
  color: var(--btnColor);
  margin: 2rem 0;
}

.sml__list i:hover{
  border: white solid 2px;
  border: var(--mainColor) solid 2px;
}

input:focus , textarea:focus{
  border-bottom: #e9204f 2px solid;
  border-bottom: var(--btnColor) 2px solid;
  padding-bottom: 0.5rem;
}

.footer__links a:hover{
  text-decoration: underline;
}

