    padding: 1rem;
    justify-self: center;
}

.navbar__logo{
    grid-row: 1;
    justify-self: flex-start;
    font-size: 1.5rem;
    font-weight: 500;
}
.navbar__mobile--menu{
    grid-row: 1;
    justify-self: flex-end;
    font-size: 1.5rem;
    font-weight: 500;
}


/* nav pc */
@media (min-width : 1025px){
    #navbar{
        padding: 1rem;
    }
