        display: flex;
grid-column: 1/-1;
display: grid;
grid-template-columns: repeat(11,1fr);
padding-top: 3rem;
    }
    .navbar__logo{
        grid-column: 1/3;
        justify-self: flex-start;
        margin-left: 1.5rem;

    }
    .navbar__logo a{
