.contact__left , .contact__right{
    justify-self: center;
    grid-column: 1/-1;
    text-align: left;
}
.contact__left h1{
    font-weight: 700;
    font-size: 2.5rem;
}
.contact__left .email{
    color: #e9204f;
font-size: 1rem;
margin-top: 1rem;
margin-bottom: 2rem;
}
.contact__left .normalFontSize{
    margin-bottom: 2rem;
}
input{
    width: 95%;
}
.sml__list{
    margin-bottom: 1rem;
}
textarea{
    margin: 2rem 0;
    margin-top: 1rem;
}
.contact__right button{
    width: 50%;
}
@media (min-width:701px) and (max-width:1024px){
    .contact__left ,.contact__right{
        justify-self: flex-start;
    }
}

@media (min-width:1025px){
    .contact__left {
        justify-self: flex-start;
        grid-column: 1/5;
    }
    .contact__left h1{
        font-size: 3.5rem;
    }
    .contact__left .email{
    font-size: 1.5rem;

    }
    .contact__left p{
        font-size: 1rem;
        width: 70%;
    }
    .contact__right{
        justify-self: flex-start;
        grid-column: 5/12;
    }
}