#footer{
    background-color: black;
    margin-top: 4rem;
}
.footer__links ,.copyright{
    justify-self: center;
    grid-column: 1/-1;
}
.copyright{
    color: #747474;
    margin: 1rem 0;
}
.copyright span{
    color: white;
}
.copyright h6{
    font-weight: 500;
    font-size: 1rem;

}
.footer__links ul{
    display: block;
}
.footer__links ul li{
    margin-bottom: 1rem;
}
.footer__links a{
    color: #747474;
}