h1,
p,
button {
  grid-column: 1/-1;
  justify-self: center;
}
#hero p {
  color: white;
  padding: 3rem 0;
  margin: 0;
}
button {
  width: 70%;
}
#hero {
  padding: 8rem 0;
  justify-self: center;
  grid-column: 1/-1;
}

@media (min-width:1025px){
  #hero p {
    width: 60%;
  }
  #hero .btn{
    width: 30%;
  }
}
