.team__container{
    justify-self: center;
    width: 200px;
        height: 200px;
    margin-bottom: 2rem;
    position: relative;
  }

.team_info{
  position: absolute;
  background-color: rgba(233, 32, 79, 0.8);
  width: 100%;
  height: 100%;
  border-radius: 50%;
display: none;
align-content: center;
justify-content: center;
}
.team_job{
margin: 1rem 0;
font-size: 55%;
}
.team_info h3{
  font-size: 110%;
}
.team_info ul{
  display: flex;
justify-self: center;
}
.team_info ul li{
  padding: 0 0.5rem;
  font-size: 100%;
}

@media (min-width:701px) and (max-width:1024px){
  #team .row{
    grid-template-columns: repeat(auto-fit,minmax(200px,1fr));
  }
}

@media (max-width:700px){
  #team .row{
    grid-template-columns: repeat(3, 1fr);
  }
  .team__container{
    grid-column: 1/-1;
  }
}

@media (min-width:1025px){
  .team__container{
    width: 270px;
    height: 270px;
  }
  #team .row{
    grid-template-columns: repeat(auto-fit,minmax(350px,1fr));
  }
}

